	module.exports = {"data":{"post":{"html":"<p>Four months ago it looked like Chancellor Schröder was on the ropes, and would soon be down and out. The economy and the budget were in ruins, he was battered  in local elections, he had a vocal but pointless policy on Iraq, he was caught between election promises and political realities, he faced dissent in his own ranks and a united opposition fully in charge of the Bundesrat. </p>\n<p>These days it all looks different. Actually, all of those conditions still hold, except now it's the Union that is in tatters, not the SPD. An <a title=\"Im Chor der Union stimmt kaum noch ein Ton\" href=\"http://www.abendblatt.de/daten/2003/06/21/178931.html\">editorial</a> (in German) in Saturday's Abendblatt describes the cacophony (a word Schröder was using 4 months ago to chastise his own party) in the Union.</p>\n<p>A couple of examples: This past week the Merkel and Stoiber did an end-run around their own health care expert Seehofer to present a most unsocial health care plan (co-payments for everything! for everybody!). The loyal Seehofer nearly resigned, and the Bundestag debate took place without him. This coming week the cabinet will be considering moving up a planned tax cut, to be financed by cutting subsidies. The mantra of the Union for the past 2 years has been cutting taxes and subsidies, but now that it looks like Schröder is going to do it, the leading figures in the Union are rushing to protect their favorite barrels of pork (tax credit for commuters! subsidies for house buyers!). Bavaria has local elections in a couple of months, and it seems the Union will be frozen in the headlights until then.</p>\n<p>There's nothing like a little policy-making responsibility (the firm Bundesrat majority) to show how much, or in this case, how little backbone an opposition has. The moderate Merkel, the conservative Koch, and the odd-man-out Stoiber are doing a strange dance to work out who is really in charge of the Union. It looks like the music will be playing until 2006, the next national elections, if not longer, and the only one smiling is Schröder.</p>","fields":{"title":"Cacophony","date":"23 Jun 2003","url":"https://papascott.de/2003-06-23-cacophony/"}}},"pathContext":{"slug":"/2003-06-23-cacophony/","prev":{"fields":{"title":"Baghdad Bulletin - Independent news from Iraq","date":"23 Jun 2003","slug":"/2003-06-23-baghdad-bulletin-independent-news-from-iraq/"}},"next":{"fields":{"title":"Internet For Poets: DNS","date":"23 Jun 2003","slug":"/2003-06-23-internet-for-poets-dns/"}}}}

